
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import SelectTab from "components/SelectTab/selectTab";
export default{
components: {
XHeader,Toast,SelectTab
},
data(){
return {
noData: false,
TabDefault1:{key:'0',val:'类型筛选'},
TabDefault2:{key:'12',val:'近一年'},
select1: [{key:'0',val:'全部类型'},{key:'1',val:'宫颈癌'},{key:'2',val:'乳腺癌'},],
select2: [{key:'1200',val:'全部'},{key:'12',val:'近一年'},{key:'36',val:'近三年'}],
type: 0,
start: "",
num: 12,
pageIndex: 1,
pageSize: 15,
list: [],
}
},
mounted() {
this.getCurrentDate();
this.getCancerList(true);
},
methods: {

selChange1(obj) {
this.type = obj.key;
this.getCancerList(true);
},

selChange2(obj) {
this.num = obj.key;
this.getCancerList(true);
},

getCurrentDate() {
var date = new Date();
var seperator = "";
var year = date.getFullYear();
var month = date.getMonth() + 1;
if (month >= 1 && month <= 9) {
month = "0" + month;
}
this.start = year + seperator + month;
},

getCancerList(upOrDown) {
if (upOrDown) {
this.pageIndex = 1;
}
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "liangaiService",
"MSH.2": "list",
"CHZBM": this.$root.loginInfo.CHZBM,
"type": this.type,
"start": this.start,
"num": this.num,
'GRDAID':this.$root.loginInfo.grdaid,
"pageIndex": this.pageIndex,
"pageSize": this.pageSize,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if(typeof res != 'undefined' && typeof res.rows != 'undefined') {
if (res.pageNumber == 1) {
this.pageIndex = 1,
this.list = [];
}
if (res.rows.length > 0) {
this.noData = false;
this.pageIndex++;
this.list = [...this.list,...res.rows];
} else {
if (res.pageNumber > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.noData = true;
}
}
}
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
})
},
cancerDetail(TYPE, ID) {
if(TYPE == 'GJ') {
this.$router.push({name: "CervicalCancers", params: {ID: ID}});
} else if (TYPE == 'RX') {
this.$router.push({name: "BreastCancers", params: {ID: ID}});
}
},
}
}



